import * as H from 'history';
import * as React from 'react';
import { Route, RouteComponentProps, withRouter } from 'react-router-dom';
import { compose } from 'redux';
import ChannelPage from './channel-page/index';
import { ChannelPlaylistForm } from './channel-playlists';
import ChannelsForm from './channels';
import HomeForm from './home';
import { PlaylistForm } from './playlist';
import SearchForm from './search';
import VideoForm from './video';
interface AppProps {
  history: H.History;
  setGlobalState: (data: any) => void;
}

class StatelessApp extends React.Component<AppProps & RouteComponentProps<any>, {}> {
  render() {
    return (
      <React.Fragment>
        <Route path={this.props.match.url + ''} exact={true} component={HomeForm} />
        <Route path={this.props.match.url + '/search/:key'} exact={true} component={SearchForm} />
        <Route path={this.props.match.url + '/channels'} exact={true} component={ChannelsForm} />
        <Route path={this.props.match.url + '/channels/:id'} exact={true} component={ChannelPage} />
        <Route path={this.props.match.url + '/playlists/:id'} exact={true} component={PlaylistForm} />
        <Route path={this.props.match.url + '/:id'} exact={true} component={VideoForm} />
      </React.Fragment>
    );
  }
}

const VideoRoutes = compose(
  withRouter,
)(StatelessApp);
export default VideoRoutes;



