import * as React from 'react';
import { useHistory } from 'react-router-dom';
import { context } from 'src/video/app';
import { Playlist } from 'src/video/video-plus';
import SlideShow from '../SlideShow';

const PlayListsHorizontal = (props: { channelId: string }) => {
  const history = useHistory();
  const videoService = context.getVideoService();
  const getChannelPlaylists = videoService.getChannelPlaylists;
  const [playlists, setPlaylists] = React.useState<Playlist[]>([]);

  React.useEffect(() => {
    (async () => {
      if (props.channelId) {
        const res = await getChannelPlaylists(props.channelId);
        setPlaylists(res.list);
      }
    })();
  }, []);

  const view = (e: any, id: string) => {
    e.preventDefault();
    history.push(`channels/${id}`);
  };
  return (
    <ul className='list-view horizon'>
      {playlists && playlists.map((item) => (
        <li key={item.id} className='video'>
          <div className='cover'>
            <SlideShow id={item.id} thumbnail={item.mediumThumbnail} thumbnailSize='mediumThumbnail' />
          </div>
          <h4 onClick={e => view(e, item.channelId)}>{item.title}</h4>
        </li>
      )
      )}
    </ul>
  );
};
export default PlayListsHorizontal;



