import * as React from 'react';
import { useParams } from 'react-router-dom';
import { storage } from 'uione';
import { context } from '../app';
import { Channel } from '../video-plus';
import './index.scss';
export default function ChannelSubscriptions() {
  // const resource = storage.resource().resource();
  const {id} = useParams();
  const videoService = context.getVideoService();
  const [data, setData] = React.useState<Channel[]>([]);
  const [nextPageToken, setNextPageToken] = React.useState<string>();
  React.useEffect(() => {
    (async () => {
      const res = await videoService.getSubcriptions(id, false, 3, undefined);
      setData(res.list);
      setNextPageToken(res.nextPageToken);
    })();
  }, []);

  const loadMore = async () => {
    if (nextPageToken) {
      const res = await videoService.getSubcriptions(id, false, 3, nextPageToken);
      setData([...data, ...res.list]);
      setNextPageToken(res.nextPageToken);
    }
  };

  return(
    <div className='channel-subscriptions'>
      <h3 className='title'>Subscriptions</h3>
      <div className='containers'>
        { data.map((x, i) => {
          return(
            <div className='box' key={i}>
              <div className='avatar'>
                <img
                  src={x.mediumThumbnail}
                  alt='Avatar Channel'
                />
              </div>
              <div className='title-channel'>{x.title}</div>
            </div>
          );
        })}
      </div>
      {nextPageToken && <button type='submit' id='btnMore' name='btnMore' className='btn-more' onClick={loadMore}>Load more</button>}
    </div>
  );
}





