import * as React from 'react';
import { Route, Switch } from 'react-router-dom';
import VideoRoutes from './video/routes';

function App() {

  return (
    <Switch>
      <Route path='/videos' component={VideoRoutes} />
      <Route path='/' component={VideoRoutes} />
    </Switch>
  );
}

export default App;



