import * as React from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { storage } from 'uione';
import { context } from './app';
import Comments from './components/Comments';
import LoadMoreButton from './components/LoadMoreButton';
import VideoInfoBox from './components/VideoInfoBox';
import { Playlist, Video } from './video-plus';

const perPage = [12, 12, 12, 12];
const max = perPage.reduce((a, b) => a + b, 0);
const videoFields = ['id', 'title', 'publishedAt', 'mediumThumbnail', 'channelId', 'channelTitle', 'categoryId'];

const VideoForm = () => {
  const { id } = useParams();
  const history = useHistory();
  // const resource = storage.resource().resource();
  const videoService = context.getVideoService();
  const [listRelated, setListRelated] = React.useState<Playlist[]>([]);
  const [video, setVideo] = React.useState<Video>();
  const [page, setPage] = React.useState(1);
  const [sliceData, setSliceData] = React.useState([]);

  React.useEffect(() => {
    (async () => {
      if (id) {
        const resVideo = await videoService.getVideo(id);
        setVideo(resVideo);
        const resRelatedVideo = await videoService.getRelatedVideos(id, max, undefined, videoFields);
        setListRelated(resRelatedVideo.list);
        setSliceData(resRelatedVideo.list.slice(0, perPage[0]));
        setPage(page + 1);
      }
    })();
  }, []);

  const back = () => {
    history.goBack();
  };

  const loadMore =  () => {
    const begin = (page - 1) * perPage[page - 1];
    const end = page * perPage[page - 1];
    setSliceData([...sliceData, ...listRelated.slice(begin, end)]);
    setPage(page + 1);
  };

  return (
    <div className='view-container'>
      <header>
        <button type='button' id='btnBack' name='btnBack' className='btn-back' onClick={back} />
        <h2>{video && video.title}</h2>
      </header>
      <div className='row list-view'>
        <div className='col s12 m12 l9 xl9'>
          <form id='videoForm' name='videoForm' style={{ paddingTop: 8 }}>
            <div className='video-container'>
              <div>
                <iframe
                  width='100%'
                  height='600'
                  src={`https://www.youtube.com/embed/${id}`}
                  title='Video Player'
                  allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
                  allowFullScreen={true}
                />
              </div>
              {
                video && <VideoInfoBox data={video}/>
              }
            </div>
          </form>
          <Comments videoId={id} />
        </div>
        <div className='col s12 m12 l3 xl3'>
          <form className='list-result'>
            <ul className='list-view'>
              {sliceData && sliceData.map((item, i) => {
                return (
                  <li key={i} className='card'>
                    <section>
                      <div className='cover' style={{ backgroundImage: `url('${item.highThumbnail}')` }} />
                      <h4>{item.title}</h4>
                    </section>
                  </li>
                );
              })}
            </ul>
            {page - 1 !== perPage.length && <button type='button' id='btnMore' name='btnMore' className='btn-more' onClick={loadMore}>Load more</button>}
          </form>
        </div>
      </div>
    </div>
  );
};
export default VideoForm;



