import * as React from 'react';
// import {getComment} from '../../services';
import { context } from '../../app';
import { Comment } from '../../video-plus';

interface CommentI {
  commentId: string;
  totalReplyCount?: number;
  authorProfileImageUrl: string;
  authorDisplayName: string;
  likeCount: number;
  textDisplay: string;
}

const CommentItem = (
  comment: CommentI
) => {
  const videoService = context.getVideoService();
  const getComment = videoService.getComments;
  const [comments, setComments] = React.useState<Comment[]>([]);
  const [show, setShow] = React.useState(false);
  const [nextPage, setNextPage] = React.useState<string>('');
  React.useEffect(() => {
    (async () => {
      if (comment.commentId && show && !comments.length) {
        const res = await getComment(comment.commentId, 5);
        setNextPage(res.nextPageToken);
        setComments(res.list);
      }
    })();
  }, [show]);

  const handleShowMore = () => {
    setShow(!show);
  };

  const loadMore = async (id: string) => {
    const res = await getComment(id, 5, nextPage);
    setNextPage(res.nextPageToken);
    const newList = [...comments].concat(res.list);
    setComments(newList);
  };

  return (
    <div className='row comments'>
      <div className='col s12 m2 l2 xl1'>
        <div className='img-user'>
          <img src={comment.authorProfileImageUrl} />
        </div>
      </div>
      <div className='col s12 m10 l10 xl11'>
        <h3 className='author'>
          {comment.authorDisplayName}
        </h3>
        <p dangerouslySetInnerHTML={{ __html: comment.textDisplay }} />
        <h5>Like: {comment.likeCount}</h5>
        {
          comment.totalReplyCount > 0 && (
            <>
              <a className='view' onClick={handleShowMore}>{show ? 'Hide' : 'View'} {comment.totalReplyCount} replies</a>
              {show && comments.map(c => (
                <>
                  <CommentItem
                    key={c.id}
                    commentId={c.id}
                    authorProfileImageUrl={c.authorProfileImageUrl}
                    authorDisplayName={c.authorDisplayName}
                    likeCount={c.likeCount}
                    textDisplay={c.textDisplay}
                  />
                </>
              ))}
            </>
          )
        }
        {nextPage && <a className='view' onClick={() => loadMore(comment.commentId)}>View more replies</a>}
      </div>
    </div>
  );
};
export default CommentItem;



