import * as React from 'react';
import {context} from 'src/video/app';
import {PlaylistVideo} from 'src/video/video-plus';
import PlayButtonCustom from '../PlayButton';
import './index.scss';

const CustomSlideShow = (props: any) => {
  const {id, thumbnail, thumbnailSize} = props;
  const videoService = context.getVideoService();
  const getPlaylist = videoService.getPlaylistVideos;
  const [currentIndex, setCurrentIndex] = React.useState(0);
  const [play, setPlay] = React.useState(false);
  const [videos, setVideos] = React.useState<PlaylistVideo[]>([]);
  const [length, setLength] = React.useState(3);
  const [fetching, setFetching] = React.useState(false);

  React.useEffect(() => {
    (async() => {
      if (id && fetching) {
        const res = await getPlaylist(id, 3);
        setVideos(res.list);
        setLength(res.list.length);
      }
    })();
  }, [fetching]);

  const next = () => {
    if (currentIndex < (length - 1)) {
        setCurrentIndex(prevState => prevState + 1);
    }
    setPlay(false);
  };

  const prev = () => {
    if (currentIndex > 0) {
        setCurrentIndex(prevState => prevState - 1);
    }
    setPlay(false);
  };

  const handlePlayVideo = () => {
    setPlay(true);
  };

  const handleFetch = () => {
    setFetching(true);
  };

  return (
      <div className='carousel-container cover' style={{width: '100%'}} onMouseEnter={handleFetch}>
          <div className='carousel-wrapper'>
            <a onClick={prev} className='left-arrow'>
              &#10094;
            </a>
            {
              !play && <div className='play-container' onClick={handlePlayVideo}>
                <PlayButtonCustom />
              </div>
            }
            <div className='carousel-content-wrapper'>
              <div className='carousel-content' style={{ transform: `translateX(-${currentIndex * 100}%)` }}>
                {
                  videos && videos.length > 0 ? (
                    videos.map((item, index) => (
                      currentIndex === index && play ? (
                        <iframe
                        key={item.title}
                        width='200'
                        height='200'
                        src={currentIndex === index ? `https://www.youtube.com/embed/${item.id}?autoplay=1` : `https://www.youtube.com/embed/${item.id}`}
                        title='YouTube video player'
                        allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture;'
                        />
                      ) : (
                        <img className='img-thumb' key={item.title} src={item[thumbnailSize]} />
                      )
                    ))
                  ) : (
                    <img src={thumbnail} />
                  )
                }
              </div>
            </div>
            <a onClick={next} className='right-arrow'>
              &#10095;
            </a>
          </div>
      </div>
  );
};
export default CustomSlideShow;



