import {MouseEvent} from 'react';

export const viewVideo = (videoId: string, e: MouseEvent<HTMLAnchorElement|HTMLButtonElement>, history: any) => {
  if (e && e.preventDefault) {
    e.preventDefault();
  }
  if (history && history.push && typeof history.push === 'function') {
    history.push(`/videos/${videoId}`);
  }
};
export const viewPlaylist = (playlistId: string, e: MouseEvent<HTMLAnchorElement|HTMLButtonElement>, history: any) => {
  if (e && e.preventDefault) {
    e.preventDefault();
  }
  if (history && history.push && typeof history.push === 'function') {
    history.push(`/videos/playlists/${playlistId}`);
  }
};
export const viewChannel = (channelId: string, e: MouseEvent<HTMLAnchorElement|HTMLButtonElement>, history: any) => {
  if (e && e.preventDefault) {
    e.preventDefault();
  }
  if (history && history.push && typeof history.push === 'function') {
    history.push(`/videos/channels/${channelId}`);
  }
};



