import axios from 'axios';
import {HttpRequest} from 'axios-core';
import {VideoClient, VideoService} from './video-plus';

const httpRequest = new HttpRequest(axios);
class ApplicationContext {
  public videoService: VideoService;
  getVideoService(): VideoService {
    if (!this.videoService) {
      this.videoService = new VideoClient('https://tube-server-v2.herokuapp.com/tube', httpRequest, 3, 3, 'AIzaSyDVRw8jjqyJWijg57zXSOMpUArlZGpC7bE');
    }
    return this.videoService;
  }
}

export const context = new ApplicationContext();



