import * as React from 'react';
import { useHistory } from 'react-router-dom';
import { Channel, Video } from 'src/video/video-plus';
import { context } from '../../app';
import './index.scss';

const channelFields = ['channelTitle', 'thumbnail'];
export default function VideoInfoBox (props: {data: Video}) {
  const history = useHistory();
  const [collapsed, setCollapsed] = React.useState(true);
  const [channel, setChannel] = React.useState<Channel>();
  const getChannel = context.videoService.getChannel;
  React.useEffect(() => {
    (async () => {
      const res = await getChannel(props.data.channelId, channelFields);
      setChannel(res);
    })();
  }, []);

  const handleClick = () => {
    setCollapsed(!collapsed);
  };
  const gotoChannel = (channelId: string) => {
    history.push(`/videos/channels/${channelId}`);
  };

  return(
    <div className='video-info-container'>
      <div className='video-info'>
        <h3 className='video-title'>{props.data.title}</h3>
        <div className='publish'>{props.data.publishedAt.toDateString()}</div>
      </div>
      <div className='video-info-box'>
        <div className='top'>
          <img
          src={channel && channel.thumbnail}
          alt='Avatar'
          width={48}
          height={48}
          />
          <div className='channel-info'>
            <h4 className='channel-name' onClick={() => gotoChannel(props.data.channelId)}>
              {props.data.channelTitle}
            </h4>
          </div>
        </div>
        <div className= {`video-description ${collapsed ? 'collapsed' : 'extended' } `}>
          <p dangerouslySetInnerHTML={{__html: props.data.description}}/>
        </div>
        <span
          className='btn-show'
          onClick={handleClick}
        >
          {collapsed ? 'SHOW MORE' : 'SHOW LESS'}
        </span>
      </div>
    </div>
  );
}



